.skills-title, .portfolio-title {
    margin-bottom: 40px;
    margin-top: 120px;
}

.skills-icon {
    color: #b62c2c;
}

.slide-left {
  -webkit-animation: slideInLeft;
          animation: slideInLeft;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}

.slide-right {
  -webkit-animation: slideInRight;
          animation: slideInRight;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}

.slide-up {
  -webkit-animation: slideInUp;
          animation: slideInUp;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}

@media only screen and (min-width: 960px) {
    .design-card-footer {
        position: absolute;
        bottom: 0px;
        width: 80%;
    }

    .design-card {
        position: relative;
    }
}


